*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
body{
    background-color: #B1D4E0;
    font-family: Verdana, Geneva, Tahoma, sans-serif;
}
main{
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: #fff;
    max-width: 500px;
    margin: 0 auto;
    margin-top: 50px;
    padding-bottom: 20px;
    border-radius: 10px;
}
h1{
    background-color: #145DA0;
    color: #fff;
    width: 100%;
    margin-bottom: 10px;
    font-size: 2rem;
    padding: 20px 10px;
    border-radius: 10px 10px 0 0;
}

.input-section{
    width: 70%;

}
.input-slider{
    width: 100%;
    margin-top: 10px;

}

.output{
    padding: 10px 5px;
    background-color: #1b1096;
    border-radius: 50px;
    color: white;
    margin-top: 10px;
    font-weight: 700;
}
.output-section{
    margin-top: 20px;
}


